.navbar {
  margin-top: 30px;
  margin-bottom: 10px;
}

.navbar__item {
  margin-left: 20px;
  margin-right: 20px;
  &:hover {
    cursor: pointer;
  }
}


.navbar__no-wallet {
  &:hover {
    cursor: auto;
    font-weight: lighter;
  }
}

.navbar__item--disabled {
  opacity: 0.6;
  &:hover {
    cursor: normal;
  }
}

